.wp-block-create-block-team-members {
	.block-editor-block-list__layout {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		.block-editor-block-list__block {
			margin: 10px;

			h4 {
				color: #222;
				font-size: 22px;
				font-size: bold;
				margin: 0;
			}

			p {
				color: #565656;
				font-size: 16px;
				font-weight: 300;
				margin-bottom: 5px;
				margin-top: 5px;
			}
			.wp-block-blocks-course-team-member-social-links {
				ul {
					list-style: none;
					padding: 0;
					margin: 15px -5px 0;

					li {
						display: inline-block;
						margin: 5px;

						span.dashicon {
							color: #4e4e4e;
						}

						&.is-selected {
							outline: 2px solid rgb(208, 40, 227);
						}

						button {
							background: none;
							border: none;
							padding: 0;
							cursor: pointer;
							margin: 0;
						}

						&.wp-block-blocks-course-team-member-add-icon-li {
							button {
								outline: 1px dashed;
							}
						}
					}
				}
			}

			.wp-block-blocks-course-team-member-link-form {
				background-color: aliceblue;
				border: 1px solid;
				padding: 10px;
				margin-top: 10px;
			}
		}
	}

	@for $i from 1 through 6 {
		div.has-#{$i}-columns {
			// Parent blocks only
			.block-editor-block-list__block {
				width: calc(100% / #{$i} - 20px);
			}

			// Nested blocks inside parent blocks
			.block-editor-block-list__block .block-editor-block-list__block {
				width: auto;
			}
		}
	}
	.block-editor-media-placeholder {
		margin-bottom: 20px;
	}
	.wp-block-blocks-course-team-member-img {
		position: relative;
		margin-bottom: 20px;

		&.is-loading img {
			opacity: 0.3;
		}

		.components-spinner {
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			margin: auto;
		}
	}
}
